<template>
  <div class="sales-board">
      <div class="sales-board-intro">
        <h2></h2>
        <p></p>
      </div>
      <div class="sales-board-form">
          <div class="sales-board-line">
              <div class="sales-board-line-left">
                  
              </div>
              <div class="sales-board-line-right">
                <v-counter  @on-change="computednum($event)" :max="100" :min="1" ></v-counter>
              </div>
          </div>
          <div class="sales-board-line">
              <div class="sales-board-line-left">
                  
              </div>
              <div class="sales-board-line-right">
                  <v-selection :selections="buyTypes"  @on-change="onbuyType($event)"></v-selection>
              </div>
          </div>
          <div class="sales-board-line">
              <div class="sales-board-line-left">
                  
              </div>
              <div class="sales-board-line-right">
                  <v-chooser
                  :selections="periodList"
                   @on-change="onversion($event)"></v-chooser>
              </div>
          </div>
          <div class="sales-board-line">
              <div class="sales-board-line-left">
                  
              </div>
              <div class="sales-board-line-right">
                  <v-mul-chooser
                  :selections="versionList"
                   ></v-mul-chooser>
              </div>
          </div>
          <div class="sales-board-line">
              <div class="sales-board-line-left">
                  
              </div>
              <div class="sales-board-line-right">
                  {{ price }} 
              </div>
          </div>
          <div class="sales-board-line">
              <div class="sales-board-line-left">&nbsp;</div>
              <div class="sales-board-line-right">
                  <div class="button">
                    
                  </div>
              </div>
          </div>
      </div>
      <div class="sales-board-des">
        <h2></h2>
        <p></p>

        <h3></h3>
        <ul>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
        </ul>

        <h3></h3>
        <ul>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
        </ul>
      </div>
      <my-dialog :is-show="isShowPayDialog" @on-close="hidePayDialog">
        <table class="buy-dialog-table">
          <tr>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
          </tr>
          <tr>
            <td>{{ buyNum }}</td>
            <td>{{ buyType.label }}</td>
            <td>{{ period.label }}</td>
            <td>
              <span v-for="item in versions">{{ item.label }}</span>
            </td>
            <td>{{ price }}</td>
          </tr>
        </table>
        <h3 class="buy-dialog-title"></h3>
        <bank-chooser @on-change="onChangeBanks"></bank-chooser>
        <div class="button buy-dialog-btn" @click="confirmBuy">
          
        </div>
      </my-dialog>
      <my-dialog :is-show="isShowErrDialog" @on-close="hideErrDialog">
        
      </my-dialog>
      <check-order :is-show-check-dialog="isShowCheckOrder" :order-id="orderId" @on-close-check-dialog="hideCheckOrder"></check-order>
  </div>
</template>

<script>
import VSelection from '../../components/base/selection'
import VCounter from '../../components/base/counter'
import VChooser from '../../components/base/chooser'
import VMulChooser from '../../components/base/multiplyChooser'
import Dialog from '../../components/base/dialog'
import BankChooser from '../../components/bankChooser'
import CheckOrder from '../../components/checkOrder'
import _ from 'lodash'
export default {
  components: {
    VSelection,
    VCounter,
    VChooser,
    VMulChooser,
    MyDialog: Dialog,
    BankChooser,
    CheckOrder
  },
  data () {
    return {
      buyNum: 0,				//
      buyType: 0,
      versions: 0,
      period: 0,
      price: 500,
      versionList: [
        {
          label: '',
          value: 0
        },
        {
          label: '',
          value: 1
        },
        {
          label: '',
          value: 2
        }
      ],
      periodList: [
        {
          label: '',
          value: 0
        },
        {
          label: '',
          value: 1
        },
        {
          label: '',
          value: 2
        }
      ],
      buyTypes: [
        {
          label: '',
          value: 0
        },
        {
          label: '',
          value: 1
        },
        {
          label: '',
          value: 2
        }
      ],
      isShowPayDialog: false,
      bankId: null,
      orderId: null,
      isShowCheckOrder: false,
      isShowErrDialog: false
    }
  },
  // buyNum: 0,        //
  //     buyType: 0,
  //     version: 0,
  //     period: 0,
  //     price: 0,

  methods: {
    onversion(val){      //3
      this.version = val
      this.price = (this.version + this.buyType)*500
      console.log(val)
    },
    onbuyType(val){       //2
      this.buyType = val
      this.price = this.price +(this.version + this.buyType)*500
      console.log(val)
    },
    computednum(num){     //1
    	this.price = this.price+(num*500)
    },
    showPayDialog () {
      this.isShowPayDialog = true
    },
    hidePayDialog () {
      this.isShowPayDialog = false
    },
    hideErrDialog () {
      this.isShowErrDialog = false
    },
    hideCheckOrder () {
      this.isShowCheckOrder = false
    },
    onChangeBanks (bankObj) {
      this.bankId = bankObj.id
    },
    confirmBuy () {
    this.isShowPayDialog = false
      let buyVersionsArray = _.map(this.versions, (item) => {
        return item.value
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.buy-dialog-title {
  font-size: 16px;
  font-weight: bold;
}
.buy-dialog-btn {
  margin-top: 20px;
}
.buy-dialog-table {
  width: 100%;
  margin-bottom: 20px;
}
.buy-dialog-table td,
.buy-dialog-table th{
  border: 1px solid #e3e3e3;
  text-align: center;
  padding: 5px 0;
}
.buy-dialog-table th {
  background: #4fc08d;
  color: #fff;
  border: 1px solid #4fc08d;
}
</style>



